// src/db.js
// Full overwrite: provides loadRecords, saveRecord, deleteRecord with robust checks and diagnostics.
// Uses @supabase/supabase-js if available, otherwise falls back to REST fetch.
// Exports: loadRecords(), saveRecord(record), deleteRecord(id), inspectProblemRows(n)

const SUPABASE_URL = process.env.REACT_APP_SUPABASE_URL;
const SUPABASE_ANON_KEY = process.env.REACT_APP_SUPABASE_ANON_KEY;

// Helper: simple logger (prints in browser console)
function log(...args){ try{ console.log("[db]",...args);}catch(e){} }

// Validate env
if (!SUPABASE_URL || !SUPABASE_ANON_KEY) {
  log("WARN: Supabase env not set. Set REACT_APP_SUPABASE_URL and REACT_APP_SUPABASE_ANON_KEY in .env.local or environment.");
}

// Try to import supabase client if available (bundled)
let supabaseClient = null;
try {
  // dynamic require will be tree-shaken in browser builds if not present; safe fallback to fetch
  // eslint-disable-next-line
  const { createClient } = require('@supabase/supabase-js');
  if (typeof createClient === "function" && SUPABASE_URL && SUPABASE_ANON_KEY) {
    supabaseClient = createClient(SUPABASE_URL, SUPABASE_ANON_KEY, { auth: { persistSession: false }});
    log("Supabase client initialized");
  }
} catch (e) {
  log("Supabase client not available; will use REST fetch fallback.", e && e.message);
}

// Table name  change if your table is named differently
const TABLE = "records";

/**
 * loadRecords - fetch all records sorted by date asc (or id)
 * returns array of records or empty array on error
 */
export async function loadRecords() {
  try {
    if (supabaseClient) {
      const { data, error } = await supabaseClient
        .from(TABLE)
        .select("*")
        .order('date', { ascending: true })
        .limit(10000);
      if (error) {
        log("loadRecords supabase error:", error);
        return [];
      }
      return Array.isArray(data) ? data : [];
    } else {
      // REST fallback: use Supabase's REST endpoint
      const url = `${SUPABASE_URL}/rest/v1/${TABLE}?select=*&order=date.asc`;
      const res = await fetch(url, {
        method: "GET",
        headers: {
          "apikey": SUPABASE_ANON_KEY,
          "Authorization": `Bearer ${SUPABASE_ANON_KEY}`,
          "Content-Type": "application/json"
        }
      });
      if (!res.ok) {
        const txt = await res.text();
        log("loadRecords REST error:", res.status, txt);
        return [];
      }
      const data = await res.json();
      return Array.isArray(data) ? data : [];
    }
  } catch (e) {
    log("loadRecords exception:", e && e.message);
    return [];
  }
}

/**
 * saveRecord - attempts to insert a record and returns:
 *   true  -> success (and record persisted)
 *   false -> known failure
 * throws on unexpected errors
 *
 * The function will:
 *  - insert into Supabase
 *  - return true only if inserted row count > 0 or response includes the row
 */
export async function saveRecord(record) {
  try {
    log("saveRecord try:", record);
    if (!record) throw new Error("no record");

    // Defensive normalization: ensure fields and types
    const payload = {
      id: record.id ? String(record.id) : String(Date.now()),
      date: record.date || (new Date()).toISOString().slice(0,10),
      hand: record.hand || null,
      result: record.result || null,
      score: Number(record.score || 0)
    };

    if (supabaseClient) {
      // insert and return the inserted row(s)
      const { data, error } = await supabaseClient
        .from(TABLE)
        .insert([payload])
        .select();
      if (error) {
        log("saveRecord supabase insert error:", error);
        return false;
      }
      if (Array.isArray(data) && data.length > 0) {
        log("saveRecord inserted:", data[0]);
        return true;
      }
      log("saveRecord: no data returned after insert");
      return false;
    } else {
      // REST fallback
      const url = `${SUPABASE_URL}/rest/v1/${TABLE}?return=representation`;
      const res = await fetch(url, {
        method: "POST",
        headers: {
          "apikey": SUPABASE_ANON_KEY,
          "Authorization": `Bearer ${SUPABASE_ANON_KEY}`,
          "Content-Type": "application/json",
          "Prefer": "return=representation"
        },
        body: JSON.stringify(payload)
      });
      const txt = await res.text();
      if (!res.ok) {
        log("saveRecord REST error:", res.status, txt);
        return false;
      }
      try {
        const data = JSON.parse(txt);
        if (Array.isArray(data) && data.length > 0) {
          log("saveRecord REST inserted:", data[0]);
          return true;
        } else {
          log("saveRecord REST unexpected response:", data);
          return false;
        }
      } catch (e) {
        log("saveRecord REST parse error:", e && e.message, "resp:", txt);
        return false;
      }
    }
  } catch (e) {
    log("saveRecord exception:", e && e.message);
    return false;
  }
}

/**
 * deleteRecord - deletes by id (string)
 */
export async function deleteRecord(id) {
  try {
    if (!id) throw new Error("id required");
    if (supabaseClient) {
      const { error } = await supabaseClient
        .from(TABLE)
        .delete()
        .eq('id', String(id));
      if (error) {
        log("deleteRecord supabase error:", error);
        throw error;
      }
      return true;
    } else {
      const url = `${SUPABASE_URL}/rest/v1/${TABLE}?id=eq.${encodeURIComponent(String(id))}`;
      const res = await fetch(url, {
        method: "DELETE",
        headers: {
          "apikey": SUPABASE_ANON_KEY,
          "Authorization": `Bearer ${SUPABASE_ANON_KEY}`,
        }
      });
      if (!res.ok) {
        const txt = await res.text();
        log("deleteRecord REST error:", res.status, txt);
        throw new Error("delete failed");
      }
      return true;
    }
  } catch (e) {
    log("deleteRecord exception:", e && e.message);
    throw e;
  }
}

/**
 * inspectProblemRows - utility for local Node scripts (keeps compatibility)
 * This returns [] in the browser (no localStorage) but exists for Node test scripts.
 */
export async function inspectProblemRows(limit = 200) {
  // try to call loadRecords and find problems
  const rows = await loadRecords();
  return (rows || []).filter(r => !r || typeof r.score === "undefined" || Number.isNaN(Number(r.score))).slice(0, limit);
}

// default export convenience (optional)
export default {
  loadRecords,
  saveRecord,
  deleteRecord,
  inspectProblemRows
};
